* {
    box-sizing: border-box;
}
.Application {
    background: padding-box;
    background-color: rgba(0, 134, 134, 0.52);
}

.navigation-sub {
    margin: 0;
    padding: 0;
    color: white;
    display: block;
}

.item{
    color: black;
    font-size: 3ch;
    display: flexbox;
    
}

.item:hover {
    color: rgb(0, 18, 98);
}
li{
    float: left;
    padding: 10px;
    /* list-style-type: none; */
}

li:hover:not(.li) {
    font-size: 2ch;
    color: black;
}

a:link {
    text-decoration: none;
  }

img {
    margin: 5px;
    float: left;
    width: 180px;
}
