* {
    box-sizing: border-box;
}
img:hover {
    border-radius: 10px;
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}
div.project {
    display: grid;
    padding: 10px;
}

div.sides {
    display: flex;
    flex-wrap: wrap;
}

div.p_right {
    box-sizing: border-box;
    flex: 50%;
    padding: 20px;
    float: right;
    position: relative;
}

div.p_left {
    position: relative;
    flex: 5%;
    float: left;
} 

div.jemsPage {
    box-sizing: border-box;
    position: absolute;
    top: 20%;
    left: 10%;
    text-align: center;
}

#tips {
    margin-left: 15%;
    width: 500px;
    height: 600px;
}

#management {
    margin-left: 10%;
    width: 400px;
    height: 300px;
}

div.Championship {
    box-sizing: border-box;
    position: absolute;
    top: 20%;
    left: 0.5%;
    text-align: center;
}